import noData from '@/components/noData'
import back from '@/components/back'
export default {
    components:{
        noData,
        back
    },
    data () {
        return {
            index: null,
            itemShow:false,
            itemList:[],
            item:[],
            HospitalList:[],
            DepartmentList:[],  //pushsaveVipData
            areaList:[],
            num:0,
            DepartmentId:this.$store.state.saveVipData&&this.$store.state.saveVipData.Department?this.$store.state.saveVipData.Department.DepartmentId:0
        }
    },
    created() {
        this.getArea()
        console.log(this.$store.state.saveVipData)
    },
    methods: {
        sort(idx) {
            if(this.index == idx) {
                this.index = null
            } else {
                this.index = idx
            }
        },
        getArea(){
            this.$get('AppointmentAPI/Appointment/GetApHospitalCity',{
            }).then( m => {
                const arr = []
                m.forEach(v => {
                    arr.push(v.CityName)
                })
                arr.unshift('全部地区')
                this.itemList = [arr]
                this.areaList = m
                let CityId = 0
                if(this.$store.state.saveVipData && this.$store.state.saveVipData.CityName){
                    this.areaList.forEach(v =>{
                        if(v.CityName === this.$store.state.saveVipData.CityName){
                            this.item[0] = v.CityName
                            CityId = v.CityId
                        }
                    })
                }else{
                    this.item[0] = '全部地区'
                    CityId = 0
                }
                this.getHospital(CityId)
            })
        },
        getHospital(CityId){
            this.$post('AppointmentAPI/Appointment/QueryApHospitalPage',{
                HospitalName:'',
                CityId,
                AppointmentTypeId:this.$store.state.saveVipData.AppointmentTypeId,
                PageIndex:1,
                PageSize:100,
            }).then(o => {
               this.HospitalList = o.ReturnData
               if(!this.HospitalList.length){
                    this.DepartmentList = []
                    return
               }
               if(this.$store.state.saveVipData && this.$store.state.saveVipData.Hospital){
                   this.getDepartment(this.$store.state.saveVipData.Hospital)
               }else{
                    this.getDepartment(this.HospitalList[0])
               }
            })
        },
        getDepartment(i,idx){
            if(!i){
                this.DepartmentList = []
                return
            }
            this.HospitalList.forEach((v,k)=>{
                if(i.HospitalId === v.HospitalId){
                    this.num = k
                    this.$store.state.saveVipData.Hospital = i
                    this.$store.dispatch('pushsaveVipData',this.$store.state.saveVipData)
                }
            })
            this.$post('AppointmentAPI/Appointment/QueryApDepartmentPage',{
                HospitalId:i.HospitalId,
                HospitalName:i.HospitalName,
                PageIndex:1,
                PageSize:100,
            }).then(o => {
                this.DepartmentList = o.ReturnData
            })
        },
        childDepartment(i){
            this.$store.state.saveVipData.Department = i
            this.$store.dispatch('pushsaveVipData',this.$store.state.saveVipData)
            this.$router.push('/vipDepartment')
            console.log(this.$store.state.saveVipData)
        },
        changeVal(){
            let a = 0
            let CityId = 0
            this.areaList.forEach(v =>{
                if(v.CityName === this.item[0]){
                    CityId = v.CityId
                    a = 1
                }
            })
            this.getHospital(CityId)
            this.$store.state.saveVipData.Hospital = null
            this.$store.state.saveVipData.CityName = this.item[0]
            this.$store.dispatch('pushsaveVipData',this.$store.state.saveVipData)
        },

    },
}



